:root {
  --color: #f44336;
  --bg: rgba(255, 255, 255, 0.8);
}

.carusel {
  position: relative;
  background-size: cover;
  width: 100vw;
  height: 100vh;
  transition: 500ms ease;
  overflow: hidden;
}

.control {
  position: absolute;
  top: 45%;
  width: 4em;
  height: 4em;
  border-radius: 20px;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--bg);
  outline: none;
  color: var(--color);
}

.prev {
  left: 3%;
}

.next {
  right: 3%;
}

.indicatorsList {
  position: absolute;
  box-sizing: border-box;
  width: 20em;
  bottom: 0%;
  left: calc(50% - calc(20em / 2));
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-inline-start: 0;
  margin-block-start: 0;
  margin-block-end: 0;
  color: var(--color);
  background: var(--bg);
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  padding: 1.2em 1em 0.5em 1em;
}

.settings {
  position: absolute;
  right: 0%;
  top: 0%;
  background: var(--bg);
  border-bottom-left-radius: 20px;
}

.showControlsButton {
  border: none;
  padding: 0.5em 1em 0em 1.1em;
  outline: none;
  background: none;
  color: var(--color);
}

.autoPlayButton {
  border: none;
  padding: 0.23em 1em;
  outline: none;
  background: none;
  color: var(--color);
}
